const IndexPage = () => {
	const [start, setStart] = useState(false);
	const [finish, setFinish] = useState(false);

	const [done, setDone] = useState(false);

	const [result, setResult] = useState({});

	const [page, setPage] = useState(0);
	const [countMap, setCountMap] = useState({});
	const [checkFuckingLoser, setCheckFuckingLoser] = useState([]);

	const order = {
		// 젖소
		가슴매니아: 1,
		//
		노인성애자: 2,
		노출광: 3,
		야외플매니아: 4,
		새디스트: 5,
		마조히스트: 6,
		남근매니아: 7,
		나르시시스트: 8,
		야동수집가: 9,
		위선자: 10,
		default: Infinity,
	};

	const onReset = () => {
		setDone(false);
		setStart(false);
		setPage(0);
		setCountMap({});
		setCheckFuckingLoser([]);
		setResult("");
	};

	const dbSaveResult = (result) => {
		// docRef.set({
		// 	gender: "M",
		// 	outcome: result,
		// })
		// db.ref('/results').push({
		// 	gender: "M",
		// 	outcome: result,
		// });
		// firebase.firestore().collection("results").add({
		// 	gender: "M",
		// 	outcome: result,
		// })
	};

	const onChange = (listCount, index) => {
		if (page < 12) {
		//count each value
		listCount.forEach((countName) => {
			let variable = 1;
			if (countName[0] === "-") {
			variable *= -1;
			countName = countName.substring(1);
			}

			if (countMap[countName] !== undefined) {
			setCountMap({
				...countMap,
				[countName]: countMap[countName] + variable,
			});
			} else {
			setCountMap({
				...countMap,
				[countName]: 1,
			});
			}
		});
		setCheckFuckingLoser([...checkFuckingLoser, index]);
		console.log(countMap);

		setPage(page + 1);

		if (page + 1 === 12) {
			const loser = checkFuckingLoser.reduce((addedArray, item) => {
			if (addedArray.length === 0) {
				addedArray.push(item);
			} else {
				if (addedArray[0] !== item) {
				addedArray.push(item);
				}
			}
			return addedArray;
			}, []).length;
			if (loser > 1) {
			const counts = Object.entries(countMap);
			counts.sort((a, b) => {
				if (a[1] > b[1]) return -1;
				else if (a[1] < b[1]) return 1;
				else {
				return (
					(order[a[0]] || order.default) - (order[b[0]] || order.default)
				);
				}
			});

			// const resultJson = gyulgwa.find(resultJson => {
			// 	return resultJson.name === counts[0][0]
			// })

			setResult(gyulgwa);
			dbSaveResult(counts[0][0]);
			} else {
			// const resultJson = gyulgwa.find(resultJson => {
			// 	return resultJson.name === '게으름뱅이'
			// })
			setResult(gyulgwa);
			dbSaveResult("게으름뱅이");
			}
		}
		}
	};
	console.log(result);

	return (
		<Layout>
			<SEO title="Home" />
			{!start ? (
				<>
				<Text
					fontSize="3rem"
					mt="80px"
					w="344px"
					textAlign="center"
					fontFamily="godoM"
				>
					당신의 숨겨진
				</Text>
				<Text fontSize="3rem" w="400px" textAlign="center" fontFamily="godoM">
					페티쉬(Fetish)는
				</Text>
				<Text fontSize="3rem" w="344px" textAlign="center" fontFamily="godoM">
					무엇인가요?
				</Text>
				<Text fontSize="16px" color="#ffe1e1" fontWeight="700">
					페티쉬로 알아보는 나의 성향 테스트
				</Text>

				<Button
					w="100%"
					fontFamily="godoM"
					my="50px"
					onClick={() => {
					setStart(!start);
					}}
					maxW="240px"
					height="52px"
					p="8px"
					fontSize="1.5rem"
					fontWeight="900"
					color="#ff4655"
					boxShadow="0 0 0 1px inset rgba(236, 232, 225, 0.3)"
				>
					시작하기
				</Button>
				<Box 
				fontFamily="godoM"
				fontSize="sm">오늘 참가하신 수는 5,192,184,324,987명 입니다</Box>

				<Text fontSize="14px" position="absolute" bottom="0px" padding="20px">
					본 테스트는 심리학 논문을 밑받침으로 만들어진 신빙성 있는 결과임이나
					절대적이진 않은 정보임을 알려드립니다.
				</Text>
				</>
			) : (
				<>
				{
					page < 12 ? (
					<>
						<Progress 
							color="pink" 
							backgroundColor="#ff9292"
							position="absolute" 
							top="0px"
							value={(page+1)/12*100} h="15px" w="100%"/>
						<Text
						fontFamily="godoB"
						position="absolute"
						color="#de4242"
						right="16px"
						>{`${page+1}/12`}</Text>
						<Flex
						h="340px"
						alignItems="center"
						flexDir="column"
						justifyContent="center"
						>
						<Text
							position="absolute"
							color="#ff000030"
							fontSize="730px"
							bottom="0px"
							fontFamily="godoB"
							right="-2%"
						>
							{" "}
							Q{" "}
						</Text>
						<Text fontSize="44px" color="#5d0000">
							<span role="img" aria-label="heart">
							&#10084;&#65039;
							</span>
						</Text>

						<Text
							fontFamily="godoM"
							fontSize="44px"
							width="85%"
							color="#5d0000"
							textAlign="center"
						>
							{dataJson[page].question}
						</Text>
						</Flex>
						<SimpleGrid columns={2} spacing={5}>
						{dataJson[page].answers.map((answer, index) => {
							return (
							<Button
								key={index}
								border="2px solid #ff9f9f"
								bg="#ff6161b3"
								fontSize="20px"
								height="150px"
								w="150px"
								color="white"
								borderRadius="20px"
								whiteSpace="break-spaces"
								fontWeight=""
								fontFamily="godoM"
								_hover={{
								backgroundColor: "#ff6161b3",
								}}
								_active={{
								backgroundColor: "#ff6161b3",
								}}
								_focus={{
								boxShadow: "0 0 0 0",
								}}
								onClick={() => {
								onChange(answer.value, index);
								}}
							>
								{answer.label}
							</Button>
							);
						})}
						</SimpleGrid>
					</>
					) : (
					<>
						{!done ? (
						<Transition setDone={setDone} />
						) : (
						<>
							<Text fontSize="42px" fontFamily="baskin">
							"{gyulgwa.name}"
							</Text>

							<Image width="250px" h="200px" src={majo} />

							<Text
							fontFamily="godoM"
							fontSize="14px"
							border="1px solid #ffffff"
							padding="16px"
							borderRadius="15px"
							borderColor="green"
							width="360px"
							>
							<List spacing={3} styleType="disc">
								<ListItem>{gyulgwa.description}</ListItem>
								<ListItem>{gyulgwa.description2}</ListItem>
							</List>
							</Text>

							<Flex my="20px">
							<Box
								pt="8px"
								background="transparent"
								marginRight="20px"
								w="150px"
								h="80px"
								// borderColor="trasnparent"
								textAlign="center"
								borderRadius="20px"
								fontSize="20px"
								fontFamily="godoB"
								backgroundImage="linear-gradient(rgba(255,255,255,.5), rgba(255,255,255,0))"
								boxShadow="0 0 0 1px #ffffff91"
							>
								최고의 파트너
							</Box>
							<Box
								pt="8px"
								background="pink"
								marginLeft="20px"
								w="150px"
								h="80px"
								backgroundColor="#ff00008f"
								borderColor="black"
								textAlign="center"
								borderRadius="20px"
								fontSize="20px"
								backgroundImage="linear-gradient(rgba(255,255,255,.5), rgba(255,255,255,0))"
								boxShadow="0 0 0 1px #ffffff91"
								fontFamily="godoM"
							>
								최악의 파트너
							</Box>
							</Flex>
							<Box
							// background= "pink"
							// marginLeft= "20px"
							w="336px"
							h="80px"
							// backgroundColor="#b10000d1"
							// borderColor="black"
							textAlign="center"
							// borderRadius="20px"
							fontSize="20px"
							// boxShadow= "0 0 0 1px #d06d6d"
							fontFamily="godoB"
							>
							나와 같은 페티쉬를 갖고 있는 사람들은?
							<Box
								fontFamily="godoB"
								border="1px solid pink"
								fontSize="md"
							>
								{12890 / 1000000} % (12890/1000000) 등급: 희귀
							</Box>
							{/* 슈퍼레어, 울트라희귀, 흔함, 레어탬,  */}
							</Box>

							<Button
							whiteSpace="break-spaces"
							border="1px solid #ffffff"
							background="transparent"
							fontFamily="godoM"
							mb="20px"
							height="40px"
							w="336px"
							onClick={onReset}
							>
							테스트 다시 하기
							</Button>
						</>
						)}
					</>
					)
					// <>
					// 	<Text fontSize="xl" > 결국 당신도 똑같은 남자였어 이런 붼태 {result}... (부끄부끄)</Text>

					// 	<Button bg="#ff69b4" height="300px" w="300px" onClick={onReset}>
					// 	근데 나 또할래
					// 	</Button>
					// </>
				}
				</>
			)}
		</Layout>
	);
};

export default IndexPage;
