import React, {useState, useEffect} from "react"
import { Flex } from "@chakra-ui/core";
import FadeIn from "react-fade-in";
import Lottie from "react-lottie";
import * as legoData from "../static/processing.json";
import * as doneData from "../static/done2.json";

const defaultOptions = {
    loop: true,
    autoplay: true,
    animationData: legoData.default,
    rendererSettings: {
        preserveAspectRatio: "xMidYMid slice"
    }
}

const defaultOptions2 = {
    loop: false,
    autoplay: true,
    animationData: doneData.default,
    rendererSettings: {
       preserveAspectRatio: "xMidYMid slice"
    }
 };



const Transition = (p) => {
    const { setDone} = p
    const [loading , setLoading] = useState(false)
    useEffect(() => {
	
		setTimeout(() => {
			setLoading(true)
			console.log('This will run after 1 second!')
			setTimeout(() => {
				setDone(true)
			 }, 2000);
		}, 2500);
		// return () => {
		// 	clearTimeout(timer)
		// }

    }, [setDone, setLoading]);
    

    return (
        <FadeIn>
            <Flex justifyContent="center" alignItems="center" h="700px">
                {!loading ? (
                    <Lottie options={defaultOptions} height={360} width={360} />
                ) : (
                    <Lottie options={defaultOptions2} height={360} width={360} />
                )}

            </Flex>
        </FadeIn>
    )
}



export default Transition